  pointer-events: all;
  text-shadow: 2px 2px 3px #0000004d;
  background: radial-gradient(at 100% 100%, #a31b1b 0%, #e85454 100%);
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 20px;
  font-size: 18px;
  display: flex;
